


















































#logo {
  @apply bg-no-repeat bg-center overflow-hidden cursor-pointer transition-default;
  text-indent: -999999999px;
  width: 222px;
  min-width: 222px;
  height: 74px;
  background-image: url('~@assets/logo.png');
  background-size: contain;
}

ul {
  li {
    @apply w-full text-center;

    a, button {
      @apply w-full cursor-pointer inline-block p-3 text-center text-lg;
    }
  }
}
